.loading--sm .loading__anim {
    position: absolute;
    height: 24px;
    width: 24px;
    clip: rect(0, 24px, 24px, 15px);
    -webkit-animation: animateLoader .5s linear infinite;
    animation: animateLoader .5s linear infinite
}

.loading--sm .loading__anim:after {
    content: '';
    position: absolute;
    height: 24px;
    width: 24px;
    clip: rect(0, 24px, 24px, 15px);
    border-radius: 50%;
    -webkit-box-shadow: inset #000 0 0 0 2px;
    box-shadow: inset #000 0 0 0 2px;
    -webkit-animation: animateLoader2 .5s linear infinite;
    animation: animateLoader2 .5s linear infinite
}

