    text-decoration: none;
    color: inherit
}

a:focus {
    outline: 0
}

input:-webkit-autofill,
select:-webkit-autofill,
textarea:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 1000px white inset !important
}

